.pp-loading-blur {
	color: transparent !important;
	animation-duration: 1.25s;
	animation-fill-mode: forwards;
	animation-iteration-count: infinite;
	animation-name: pp-blur-content;
	animation-timing-function: linear;
	background: darkgray;
	background: linear-gradient(to right, #eee 10%, #ddd 18%, #eee 33%);
	background-size: 800px 104px;
	height: 100%;
	width: 100%;
	position: relative;
}

@keyframes pp-blur-content {
	0% {
		background-position: -468px 0;
	}

	100% {
		background-position: 468px 0;
	}
}

// The WC align="right" prop does not work for the payment method icons component. This is a workaround.
.wc-block-components-payment-method-icons.pp-label-icon {
	flex-grow: 1;
	justify-content: right;
	padding-right: 1em;
}
